import React from "react"
import RecipeList from "./RecipeList";
import {useState, useEffect} from 'react';
import "./App.css";


function Home() {

    const APP_ID = "117c191a";
    const APP_KEY = "1ac3a91b06022fd5506ebb997044d3c2";
  
    const [recipes, setRecipes] = useState([]);
    const [search, setSearch] = useState("");
    const [query, setQuery] = useState("lemon");
  
    useEffect(() => {
      getRecipes();
    }, [query]);
  
    const getRecipes = async () => {
      const response = await fetch(
        `https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`
      );
      const data = await response.json();
      console.log(data.hits);
      setRecipes(data.hits);
    };
  
    const updateSearch = (event) => {
      setSearch(event.target.value);
    };
  
    const getSearch = (event) => {
      event.preventDefault(); 
      setQuery(search);
      setSearch(""); 
    };

    return (
        <>
        <div className ="App">
       <h1 className="title-head">Welcome Fellow Foodie!</h1>
       <h4 className="subtitle">To start, search an ingredient to get inspiration for your next meal.</h4>
      <form onSubmit={getSearch} className="search-form">
        <input
          placeholder="Search an Ingredient"
          type="text"
          className="search-bar"
          value={search}
          onChange={updateSearch}>     
        </input>
        <button className="search-button">Search</button>
      </form>
      <div className="recipes">
        {recipes.map(recipe => (
          <RecipeList
            key={recipe.recipe.label}
            title={recipe.recipe.label}
            calories={recipe.recipe.calories}
            image={recipe.recipe.image}
            ingredients={recipe.recipe.ingredients}></RecipeList>))}
        </div>
        </div>
        </>
    );
        };
  
  
  export default Home;




